@mixin flexCenter($direction) {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: $direction;
}

@mixin tagDefault {
	color: $primary-color;
	background-color: white;
	border: 0.5px solid #c4c4c4;
	border-radius: 11px;
	margin: 0.2em;
	padding: 0.25em 0.5em;
	transition: 225ms;
}

@mixin tagTarget {
	color: white;
	background-color: $primary-color;
}

@mixin btnDefault {
	font-weight: 700;
	font-size: 1em;
	color: #fafafa;
	background-color: $primary-color;
	padding: 1em 0.5em;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}

@mixin btnTarget {
	font-weight: 700;
	color: #000;
	background-color: $quaternary-color;
	cursor: pointer;
	transition: 225ms;
}

@mixin phone {
	@media only screen and (max-width: 480px) {
		@content;
	}
}

@mixin tablet {
	@media only screen and (min-width: 481px) and (max-width: 782px) {
		@content;
	}
}
@mixin laptop {
	@media only screen and (min-width: 783px) and (max-width: 1024px) {
		@content;
	}
}

@mixin desktop {
	@media only screen and (min-width: 1025px) {
		@content;
	}
}
